$palette: (
    neutral: (
        0 : #FFF,
        10: #FCFCFC,
        50: #F5F5F5,
        100: #F0F0F0,
        1000: #000,
    ),
    main:(
        primary: #0D171F,
        on-primary: #FFF,
        primary-variant: #182939,
        primary-variant-hover: #465461,
        primary-variant-container: #0A1118,
        primary-container-1: #B7BDC2,
        primary-container-2: #959DA4,
        primary-container-hover: #E8EAEB,
        secondary: #1B7C5D,
        secondary-hover: #66A792,
        on-secondary: #FFF,
        secondary-container: #B7C5C8,
        on-secondary-container: #135842,
        secondary-variant: #A3FFBD,
        on-secondary-variant: #135842,
        secondary-variant-container: #418C56,
        on-secondary-variant-container-1: #F0F0F0,
        on-secondary-variant-container-2: #FFFFFF,
        tertiary: #EBB55F,
        on-tertiary: #0D171F,
        tertiary-container: #F4D5A3,
        on-tertiary-continer: #182939,
    ),
);

$text: ( // size, weight, height, spacing
    display:(
        1: (68px, 800, 72px, 0px),
        1-small: (56px, 800, 64px, 0px),
        2: (28px, 600, 32px, 0px),
        2-small: (28px, 600, 32px, 0px),
    ),
    heading:(
        1:(33px, 400, 48px, 0.5px),
        1-small:(29px, 400, 40px, 0.5px),
        2:(29px, 400, 40px, 0.5px),
        2-small:(26px, 400, 40px, 0.5px),
        3:(26px, 400, 40px, 0.5px),
        3-small:(23px, 400, 36px, 0.5px),
        4:(23px, 400, 36px, 0.5px),
        4-small:(20px, 400, 32px, 0.5px),
        5:(20px, 400, 32px, 0.5px),
        5-small:(18px, 400, 28px, 0.5px),
        6:(18px, 400, 28px, 0.5px),
        6-small:(16px, 400, 28px, 0.5px),
    ),
    body: (
        large: (18px, 400, 28px, 0px),
        large-semibold: (18px, 600, 28px, 0px),
        small: (16px, 400, 24px, 0px),
        small-semibold: (16px, 600, 24px, 0px),
        caption: (14px, 400, 16px, 0px),
        caption-semibold: (14px, 600, 16px, 0px),
        caption-small: (13px, 400, 16px, 0px),
        caption-small-semibold: (13px, 600, 16px, 0px),
        footer: (14px, 400, 16px, 0px),
        footer-semibold: (14px, 600, 16px, 0px),
    ),
);

@function get-color($type, $tone){
    @return map-get(map-get($palette, $type), $tone);
}

@mixin set-text($type, $size){
    $text: map-get(map-get($text, $type), $size);
    font-size: nth($text, 1);
    font-weight: nth($text, 2);
    line-height: nth($text, 3);
    letter-spacing: nth($text, 4);
    @if ($type == display and ($size == 2 or $size == 2-small)){font-family: "Baskervville SC", serif;}
    @else if ($type == header){font-family: "Baskervville", serif;}
    @else{font-family: "Lato", serif;} 
}