@media screen and (max-width: 843px){
    .photobooth{
        .video-container{
            // margin-top: 20px;
            // width: 100%;
            // max-width: 100%;
            min-height: 200px;
            max-height: calc(100dvh - 220px);
            // height: 100%;
            // height: calc(100dvh - 220px);
            .customize{
                justify-content: center;
                gap: 24px;
                padding: 8px 8px 12px;
                .container{
                    width: 100%;
                    & > * {
                        width: 100%;
                        max-width: 140px;
                    }

                    &:first-child{justify-content: flex-end}

                    .heading{@include set-text(body, caption-small-semibold);}
                    select{
                        &, option{@include set-text(body, small);}
                        border-radius: 4px;
                        height: 40px;
                        outline: none;
                        width: 100%;
                        padding: 0 8px;
                        color: black;
                        &, &:active{background-color: white;}
                        // text-align: right;
                    }
                }
            }

            &.load{
                width: 100%;
                // video, .customize{opacity: 0;}
            }

            // .video-temp{opacity: 0; justify-self: center;}
        }
        .capture{
            position: relative;
            flex-direction: column;
            justify-content: flex-start;
            width: 100%;
            gap: 20px;

            max-height: 100%;
            // min-height: 280px;

            .container{
                display: grid;
                gap: 20px;
                grid-template-columns: repeat(2, calc((100% + 20px) / 2 - 20px));
                grid-template-rows: auto;

                width: 100%;
                
                height: calc((100% - 96px + 20px) / 2 - 20px);
                min-height: 40px;

                // justify-content: center;
                figure{
                    min-height: 0px;
                    max-width: 100%;
                    // max-width: calc((100% + 20px) / 2 - 20px);

                    max-height: 100%;

                    justify-self: center;

                    // width: fit-content;
                    // max-height: 80px;
                    // height: fit-content;

                    // img{height: 100%;}
                }
            }

            .control{
                position: absolute;
                bottom: 20px;
                justify-self: center;
                .btn-primary.btn-icon{
                    @include set-text(display, 1-small);
                    width: 80px;
                    height: 80px;
        
                    i.fa{
                        font-size: 40px;
                        line-height: 40px;
                        margin-bottom: 2px;
                    }

                    &:not(:disabled):hover{
                        i.fa{
                            font-size: 44px;
                            line-height: 44px;
                        }
                    }
                }
            }
        }
    }
}

@media screen and (min-width: 844px) {
    .photobooth{
        margin: 0 80px;
        .video-container{
            .video-temp{max-width: 100%;}
            .customize{
                gap: 40px;
                padding: 16px 24px 24px;
                .container{
                    width: calc((100% - 40px) / 2 + 40px);
                    .heading{@include set-text(body, caption-semibold);}
                    ul{
                        gap: 16px;
                        label.btn-secondary{
                            @include set-text(body, large-semibold);
                            padding: 4px 12px;
                        }
                    }
                }
            }
        }
        
        .capture{
            gap: 40px;
            height: 160px;
            .container{
                gap: 20px;
                max-height: 200px;
                width: calc((100% - 40px) / 2 + 40px);
                figure{
                    max-width: calc((100% + 20px) / 2 - 20px);
                    max-height: 80px;
                }
            }

            .btn-primary.btn-icon{
                @include set-text(display, 1);
                width: 120px;
                height: 120px;
                transition: box-shadow 0.2s ease;
    
                i.fa{
                    font-size: 52px;
                    line-height: 52px;
                    margin-bottom: 4px;
                }
    
                &:not(:disabled):hover{
                    i.fa{
                        font-size: 56px;
                        line-height: 56px;
                    }
                }
            }
        }
    }
}

.photobooth{
    height: 100dvh; // omfg thank you D
    // overflow-y: hidden;
}

.video{
    width: 100%;
    &, &-temp{
        border-radius: 8px 8px 0 0;
        // transform: scaleX(-1);
        object-fit: cover;
        // aspect-ratio: 3/2;
        height: 100%;
        // height: 500px;
        
    }
    &-temp{background-color: orange;}
    

    &-container{
        justify-self: center;
        // background-color: lightgray;
        position: relative;
        aspect-ratio: 3/2;
        // min-height: 280px; // move this to big devices
        
        // width: min(100%, 150vh); // needed initially so it doesn't jump to size once all the refs are valid
        line-height: 0; // PUT THIS BACK AFTER SET-TEXT IS ESTABLISHED!

        &.load{
            width: 100%;
            video, .customize{opacity: 0; display: none;}
        }
        .video-temp{opacity: 0; justify-self: center;}

        .flash{
            position: absolute;
            top: 0;
            bottom: 0;
            right: 0;
            left: 0;
            background-color: white;
            opacity: 0;
            animation: flash 0.8s ease-out;

            @keyframes flash{
                from {
                    opacity: 0.8;
                }
            
                to {
                    opacity: 0;
                }
            }
        }

        .customize{
            position: absolute;
            left: 0;
            right: 0;
            bottom: 0;
            display: flex;
            background-color: rgba(0,0,0,0.4);
            -webkit-backdrop-filter: blur(5px);
            backdrop-filter: blur(5px);
    
            .container{
                display: flex;
    
                & > *{
                    display: flex;
                    flex-direction: column;
                    gap: 8px;
                }
    
                .heading{color: white; cursor: default;}
                select, option{cursor: pointer;}
                ul{
                    display: flex;
                    flex-flow: row wrap;
                    list-style-type: none;

                    input[type=radio]{display: none;}
                    input[type=radio]:checked + label{
                        background-color: orange;
                    }
                }
            }
        }
    }
}

.capture{
    display: flex;
    justify-content: center;
    align-items: center;
    justify-self: center;

    cursor: default;
    background-color: white;

    border-radius: 0 0 8px 8px;

    // outline: 2px solid black;

    padding: 20px;
    
    .control{
        width: fit-content;
        animation: show 0.5s ease-out;
        // animation-delay: 1.5s;

        @keyframes show{
            from {
                opacity: 0;
            }
        
            to {
                opacity: 1;
            }
        }
        .btn-primary.btn-icon{
            &:not(:disabled):hover{
                background-color: gray;
                box-shadow: 0 0 2px 2px rgba(0,0,0,0.4);
            }
            &:disabled{
                cursor: not-allowed;
                &.usermediaerror, &.capturing{background-color: red;}
            }
        }
        button.capturing{
            // transition: background-color 1s ease;

            // animation: capturing 1s ease infinite;
            // @keyframes capturing{
            //     from {
            //         background-color: orange;
            //     }
            
            //     to {
            //         background-color: red;
            //     }
            // }
        }
        i.fa{transition: font-size .2s ease;}
    }
    .container{
        display: flex;
        figure{
            line-height: 0;
            background-color: black;
            aspect-ratio: 3/2;
            img{
                width: 100%;
                // width: calc((100% + 20px) / 2 - 20px);
                // max-height: 80px;
                object-fit: cover;
                aspect-ratio: 3/2;
            }
        }
    }
}